import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import http from '@/common/request.js';
import uniSection from '@/components/uni-section/uni-section.vue';
import uniIcons from '@/components/uni-icons/uni-icons.vue';
import uniFab from '@/components/uni-fab/uni-fab.vue';

import myeditor from '../../../components/student/editor.vue';

export default {
	created() {
		this.reloadMyArtifact();
	},

	components: {
		uniNavBar,
		uniFab,
		myeditor
	},

	data() {
		return {
			list_myArtifact: [],

			// 是否显示编辑器
			show_editor: false,

			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#007AFF',
				buttonColor: '#007AFF'
			},
			fab_content: [
				// {
				// 	iconPath: '/static/component.png',
				// 	selectedIconPath: '/static/componentHL.png',
				// 	text: '作品上传',
				// 	active: false
				// },
				{
					iconPath: '/static/api.png',
					selectedIconPath: '/static/apiHL.png',
					text: '在线编写',
					active: false
				}
			]
		};
	},
	methods: {
		addArtifact(t, item) {
			let that = this;
			if (t == 'confirm') {
				that.addArtifact('close');
			}

			if (t == 'close') {
				that.show_editor = false;
			}
		},
		trigger(e) {
			if (e.index == 0) {
				// 如过是在线编写
				// this.show_editor = true;
				uni.navigateTo({
					url: '../addArtifact/addArtifact'
				});
			} else if (e.index == 1) {
				// 如果是上传文档
			}
		},
		// 跳转 我的作品详情
		toArticatDetail(item) {
			uni.navigateTo({
				url: '../myArtifactDetail/myArtifactDetail?id=' + item.StudentWorkCode
			});
		},
		back() {
			uni.navigateBack({});
		},
		// 刷新我的作品
		reloadMyArtifact() {
			let that = this;
			http.request({
				url: '/api/StudentWork/GetStudentStudentWorkList?semesterId=&search=&reviewFlag=100&paiXu=2&pageIndex=1&pageSize=999'
			}).then(r => {
				r = that.fr(r);
				if (r.Code == 200) {
					that.list_myArtifact = r.Data || [];
				}
			});
		}
	}
};
